.program__list {
  --card-top: 40px;

  @include mq(tab) {
    --card-top: 30px;
  }

  @include mq(desk) {
    --card-item: 5;
    --card-left: 46px;
  }
}

.program__item-list {
  --card-top: 8px;

  @include mq(tab) {
    --card-item: 3;
    --card-left: 50px;
  }

  @include mq(desk) {
    --card-item: 1;
    --card-top: 25px;
  }
}

.program__item-title {
  margin-bottom: 5px;

  @include mq(desk) {
    margin-bottom: 25px;
  }
}

.program__item-subtitle {
  margin-bottom: 5px;

  @include mq(tab) {
    margin-bottom: 10px;
  }
}

